<template>
<v-app>
    <v-tabs v-model="selectedKind" background-color="orange darken-3" center-active dark>
        <v-tab v-for="kind in kinds" :key="kind.id">
            <v-badge v-model="kind.anyOrdered" color="red accent-3" :content="getOrderdKindCount(kind)">
                {{ kind.display }}
            </v-badge>
        </v-tab>

        <v-tab-item v-for="kind in kinds" :key="kind.id">
            <v-expansion-panels v-model="openedPanel">
                <v-expansion-panel v-for="item in items.filter(i => i.kindId === kind.id)" :key="item.sn">
                    <v-expansion-panel-header>
                        <template>
                            <v-row no-gutters>
                                <v-col cols="12" class="font-weight-medium">
                                    <v-badge color="success" icon="mdi-check" v-model="item.isOrdered">
                                        {{ item.display }}
                                    </v-badge>
                                </v-col>
                            </v-row>
                        </template>
                    </v-expansion-panel-header>
                    <v-expansion-panel-content>
                        <v-row>
                            <v-col cols="6">
                                <v-btn-toggle rounded mandatory color="red" v-model="item.sizeIdx">
                                    <v-btn v-for="(size, idx) in item.size" :value="idx" :key="size.sn" @click.native="btnSizeOnClick(item)" style="min-width: 0">
                                        <span class="font-weight-black">{{ size.text }}</span>
                                    </v-btn>
                                </v-btn-toggle>
                            </v-col>
                            <v-col cols="2">
                                <v-dialog persistent>
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-btn v-bind="attrs" v-on="on" color="primary" style="min-width: 10px">
                                            料
                                        </v-btn>
                                    </template>
                                    <template v-slot:default="dialog">
                                        <v-card>
                                            <v-card-title>
                                                <span class="text-h5">選擇料理</span>
                                            </v-card-title>
                                            <v-card-text>
                                                <v-container>
                                                    <v-row>
                                                        <v-col cols="12" class="text-center">
                                                            <v-btn-toggle v-for="n in Math.ceil(item.add.length/6)" :key="n" rounded multiple color="deep-purple accent-3" v-model="item.addIdxArr">
                                                                <v-btn v-for="(add, idx) in item.add.slice((n-1)*6, n*6)" :value="idx" :key="add.id" @click.native="btnAddOnClick(item)">
                                                                    <span class="font-weight-black">{{ add.text }}</span>
                                                                </v-btn>
                                                            </v-btn-toggle>
                                                        </v-col>
                                                    </v-row>
                                                </v-container>
                                            </v-card-text>
                                            <v-card-actions>
                                                <v-spacer></v-spacer>
                                                <v-btn color="blue darken-1" text @click="dialog.value = false">
                                                    取消
                                                </v-btn>
                                                <v-btn color="blue darken-1" text @click="dialog.value = false">
                                                    儲存
                                                </v-btn>
                                            </v-card-actions>
                                        </v-card>
                                    </template>
                                </v-dialog>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field placeholder="數量" type="number" inputmode="numeric" patter="[0-9]*" label="數量" dense v-model="item.Q" @change="txtQOnChange(item)">
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <v-btn icon color="success" @click="btnAddProdOnClick(item)">
                                    <v-icon x-large>mdi-plus-circle</v-icon>
                                </v-btn>
                            </v-col>
                        </v-row>
                    </v-expansion-panel-content>
                </v-expansion-panel>
            </v-expansion-panels>
        </v-tab-item>
    </v-tabs>
    <v-footer class="orange lighten-5" fixed padless max-height="200px" min-height="68px">
        <v-card width="100%" flat class="orange lighten-5 text-center">
            <v-row dense align="center">
                <v-col class="pa-0" cols="2">
                    <v-card flat class="orange lighten-5 text-center">
                        <v-card-title class="pt-0 pb-0 pl-2 pr-0">                            
                            <v-dialog persistent>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn v-bind="attrs" v-on="on" id="seat" icon x-large title="座位" class="font-weight-bold">
                                        <v-icon>mdi-seat</v-icon>
                                        <div>座位</div>
                                    </v-btn>
                                </template>
                                <template v-slot:default="dialog">
                                    <v-card>
                                        <v-card-title>
                                            <span class="text-h5">選擇座位</span>
                                        </v-card-title>
                                        <v-card-text>
                                            <v-container>
                                                <v-row>
                                                    <v-col class="text-center">
                                                        <v-btn-toggle v-for="n in Math.ceil(seats.length/6)" :key="n" rounded multiple color="deep-purple accent-3" v-model="selectedSeat">
                                                            <v-btn v-for="seat in seats.slice((n-1)*6, n*6)" :value="seat.id" :key="seat.id">
                                                                <span class="font-weight-black">{{ seat.text }}</span>
                                                            </v-btn>
                                                        </v-btn-toggle>
                                                    </v-col>
                                                </v-row>
                                            </v-container>
                                        </v-card-text>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="blue darken-1" text @click="dialog.value = false">
                                                取消
                                            </v-btn>
                                            <v-btn color="blue darken-1" text @click="dialog.value = false">
                                                儲存
                                            </v-btn>
                                        </v-card-actions>
                                    </v-card>
                                </template>
                            </v-dialog>                        
                        </v-card-title>
                    </v-card>
                </v-col>
                <v-col cols="3" class="pa-0">
                    <v-card flat class="orange lighten-5 text-center">
                        <v-card-text class="pa-2">{{ getSelectedSeat() }}</v-card-text>
                    </v-card>
                </v-col>
                <v-col cols="2" class="pa-0">
                    <v-card flat class="orange lighten-5 text-center">
                        <v-card-title class="pa-0"><v-btn id="money" icon x-large title="金額" class="font-weight-bold">
                            <v-icon>mdi-cash-usd</v-icon>
                            <div>金額</div>
                        </v-btn></v-card-title>
                    </v-card>
                </v-col>
                <v-col cols="3" class="pa-0">
                    <v-card flat class="orange lighten-5 text-left">
                        <v-card-title class="pa-4">
                            <v-badge v-model="anyOrdered" color="red accent-3" :content="getOrderdCount()" bottom>
                               {{ getTotal }} 元
                            </v-badge>
                        </v-card-title>
                    </v-card>
                </v-col>
                <v-col cols="2" class="pa-0">
                    <v-card flat class="orange lighten-5 text-center">
                        <v-card-title class="pa-0">
                            <v-dialog persistent>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn v-bind="attrs" v-on="on" id="cash_reg" icon x-large title="結帳" class="font-weight-bold">                                    
                                        <v-icon>mdi-cash-register</v-icon>
                                        <div>結帳</div>
                                    </v-btn>
                                </template>
                                <template v-slot:default="dialog">
                                    <v-card>
                                        <v-card-title>
                                            <span class="text-h5">結帳</span>
                                        </v-card-title>
                                        <v-card-text>
                                            <v-container>
                                                <v-row>
                                                    <v-col>
                                                        <p class="text-h6 text--primary">餐點：{{ getOrderdCount() }}份</p>
                                                    </v-col>
                                                </v-row>
                                                <v-row>
                                                    <v-col>
                                                        <p class="text-h6 text--primary">金額：{{ getTotal }}元</p>
                                                    </v-col>
                                                </v-row>
                                                <v-row>
                                                    <v-col>
                                                        <p class="text-h4 text--primary">
                                                            <v-text-field outlined dense prefix="$" suffix="元" label="收款金額：" placeholder="收款金額："
                                                                type="number" inputmode="numeric" patter="[0-9]*" class="input-align-right"
                                                                v-model="inputCash"
                                                                :rules="[rules.required, checkInputCashGtTotal()]">
                                                            </v-text-field>
                                                        </p>
                                                    </v-col>
                                                </v-row>
                                            </v-container>
                                        </v-card-text>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="blue darken-1" text @click="dialog.value = false">
                                                取消
                                            </v-btn>
                                            <v-btn color="blue darken-1" text @click="dialog.value = false; giveChangeDialog = true">
                                                確定
                                            </v-btn>
                                        </v-card-actions>
                                    </v-card>
                                </template>
                            </v-dialog>

                            <v-dialog v-model="giveChangeDialog">
                                <v-card>
                                        <v-card-title>
                                            <span class="text-h5">應找金額</span>
                                        </v-card-title>
                                        <v-card-text>
                                            <v-container>
                                                <v-row>
                                                    <v-col>
                                                        <p class="text-h6 text--primary">總計：{{ getTotal }}元</p>
                                                    </v-col>
                                                </v-row>
                                                <v-row>
                                                    <v-col>
                                                        <p class="text-h6 text--primary">收款：{{ inputCash }}元</p>
                                                    </v-col>
                                                </v-row>
                                                <v-row>
                                                    <v-col>
                                                        <p class="text-h4 red--text">應找：$ {{ parseInt(inputCash, 10) - parseInt(getTotal, 10) }}元</p>
                                                    </v-col>
                                                </v-row>
                                            </v-container>
                                        </v-card-text>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="blue darken-1" text @click="giveChangeDialog = false">
                                                確定
                                            </v-btn>
                                        </v-card-actions>
                                    </v-card>
                            </v-dialog>
                        </v-card-title>
                    </v-card>
                </v-col>
            </v-row>
        </v-card>
    </v-footer>
</v-app>
</template>

<style scope>
    i.v-icon.v-icon {
        color: white;
    }

    #seat .v-btn__content,
    #money .v-btn__content,
    #cash_reg .v-btn__content {
        display:flex;
        flex-direction:column;
    }
    
    .scopedBorder {
        border: 1px;
        border-style: solid;
    }

    .input-align-right input {
        text-align: right;
        font-size: 2em;
    }
</style>

<script>
export default {
    name: 'OrderPage',
    data: () => ({
        tab: null,
        kinds: [],
        items: [],
        seats: [],
        prod_count: 0, //畫面上餐點數量
        selectedKind: null, //選擇的餐類類別
        openedPanel: null, //展開的餐點sn
        anyOrdered: false, //是否已點餐
        selectedSeat: [], //選擇的餐類類別
        inputCash: null, //收款金額
        rules: {
          required: value => !!value || '必須輸入！',
        }, //檢查條件
        giveChangeDialog: false, //找錢對話框
    }),
    methods: {
        //以下 - 類別class
        //餐點類別
        kindObj(id, text) {
            this.id = id;
            this.text = text;
            this.display = text;
            this.anyOrdered = false;
        },
        //餐點
        itemObj(id, sn, kindId, text, size, sizeDefaultIdx, add, addDefaultIdxArr, Q) {
            this.id = id;
            this.sn = sn;
            this.kindId = kindId;
            this.text = text;
            this.size = size;
            this.sizeDefaultIdx = sizeDefaultIdx; //int
            this.sizeIdx = sizeDefaultIdx; //int
            this.add = add;
            this.addDefaultIdxArr = addDefaultIdxArr; //int array
            this.addIdxArr = addDefaultIdxArr; //int array
            this.Q = Q;
            this.display = text + ' - ' + size[sizeDefaultIdx].price;
            this.isOrdered = false;
            this.subtotal = this.size[this.sizeIdx].price;
        },
        //規格尺寸
        sizeObj(id, text, price) {
            this.id = id;
            this.text = text;
            this.price = price;
        },
        //加料
        addObj(id, text, price, display) {
            this.id = id;
            this.text = text;
            this.price = price;
            this.display = display;
        },
        //座位
        seatObj(id, text) {
            this.id = id;
            this.text = text;
        },
        //以上 - 類別

        getSelectedSize(item) {
            return item.size[item.sizeIdx];
        },
        getSelectedAdd(item) {
            let arrAdd = [];
            item.addIdxArr.forEach(ia => {
                arrAdd.push(item.add[ia]);
            });
            return arrAdd;
        },
        setTitle(item) {
            if (isNaN(parseInt(item.Q, 10)) || item.Q.toString() === '0') {
                item.display = item.text + ' - ' + item.size[item.sizeIdx].price;
            } else {
                let strAdd = '';
                let objSize = this.getSelectedSize(item);
                let new_price = objSize.price;
                item.addIdxArr.forEach(ia => {
                    strAdd += ',' + item.add[ia].display;
                    new_price += item.add[ia].price;
                });

                let display = item.text + '(' + objSize.text + ')' + objSize.price + strAdd + ' - ' + new_price + ' *' + item.Q;
                setTimeout(() => {
                    item.display = display;
                });
            }
        },
        findItemsIdx(sn) {
            return this.items.findIndex(i => i.sn === sn);
        },
        btnAddProdOnClick(item) {
            if (!isNaN(parseInt(item.Q, 10)) && item.Q !== 0) {
                this.setTitle(item);
                let new_item = new this.itemObj(item.id, this.prod_count++, item.kindId, item.text, item.size, item.sizeDefaultIdx, item.add, item.addDefaultIdxArr, null);
                this.items.splice(this.findItemsIdx(item.sn) + 1, 0, new_item);
                this.openedPanel = null;
            }
        },
        txtQOnChange(item) {
            this.setTitle(item);
            item.isOrdered = (!isNaN(parseInt(item.Q, 10)) && item.Q > 0);
        },
        btnSizeOnClick(item) {
            this.setTitle(item);
        },
        btnAddOnClick(item) {
            if (!isNaN(parseInt(item.Q, 10)) && item.Q !== 0)
                this.setTitle(item);
        },
        getOrderdKindCount(kind) {
            let itemOrdered = this.items.filter((i) => i.isOrdered && i.kindId === kind.id);
            let len = itemOrdered.length;
            if (len > 0)
                kind.anyOrdered = true;
            else
                kind.anyOrdered = false;
            let ct = 0;
            itemOrdered.forEach(i => ct += parseInt(i.Q, 10));
            return ct;
        },
        getOrderdCount() {
            let itemOrdered = this.items.filter((i) => i.isOrdered);
            let len = itemOrdered.length;
            if (len > 0)
                this.anyOrdered = true;
            else
                this.anyOrdered = false;
            let ct = 0;
            itemOrdered.forEach(i => ct += parseInt(i.Q, 10));
            return ct;
        },
        getSubtotal(item) {
            item.subtotal = item.size[item.sizeIdx].price;
            item.addIdxArr.forEach(ia => {
                    item.subtotal += item.add[ia].price;
                });
            return item.subtotal * item.Q;
        },
        getSelectedSeat() {
            if (this.selectedSeat.length === 0)
                return '';
            else
                return this.selectedSeat.join();
        },
        checkInputCashGtTotal() {
            return parseInt(this.inputCash, 10) >= parseInt(this.getTotal, 10) || '收款金額必須大於等於結帳金額！';
        }
    },
    computed: {
        getTotal() {
            return this.items.reduce((accumulator, currentValue) => currentValue.isOrdered ? accumulator + this.getSubtotal(currentValue) : accumulator, 0);
        }
    },
    mounted() {
        let kindOb0 = new this.kindObj(0, '飯');
        let kindOb1 = new this.kindObj(1, '麵');
        let kindOb2 = new this.kindObj(2, '水餃');
        let kindOb3 = new this.kindObj(3, '湯');
        let kindOb4 = new this.kindObj(4, '小菜');
        let kindOb5 = new this.kindObj(5, '飲料');
        this.kinds.push(kindOb0);
        this.kinds.push(kindOb1);
        this.kinds.push(kindOb2);
        this.kinds.push(kindOb3);
        this.kinds.push(kindOb4);
        this.kinds.push(kindOb5);

        let addObj0 = new this.addObj(0, '加飯10', 10, '飯10');
        let addObj1 = new this.addObj(1, '加蛋10', 10, '蛋10');
        let addObj2 = new this.addObj(2, '滷汁', 0, '滷汁');
        let addObj3 = new this.addObj(3, '芝麻', 0, '芝');
        this.items.push(
            new this.itemObj(0, this.prod_count++, 0, '排骨飯',
                [new this.sizeObj(0, '小', 65), new this.sizeObj(1, '中', 75), new this.sizeObj(2, '大', 80)],
                1,
                [addObj0, addObj1, addObj2, addObj3],
                [],
                null)
        );
        this.items.push(
            new this.itemObj(1, this.prod_count++, 0, '爌肉飯',
                [new this.sizeObj(3, 'S', 65), new this.sizeObj(4, 'M', 75), new this.sizeObj(5, 'L', 80), new this.sizeObj(6, 'XL', 85)],
                1,
                [addObj0, addObj1],
                [],
                null)
        );
        this.items.push(
            new this.itemObj(2, this.prod_count++, 1, '餛飩麵',
                [new this.sizeObj(0, '小', 35), new this.sizeObj(1, '中', 40), new this.sizeObj(2, '大', 50)],
                1,
                [addObj1],
                [],
                null)
        );

        let seatObj0 = new this.seatObj('A1', 'A1');
        let seatObj1 = new this.seatObj('A2', 'A2');
        let seatObj2 = new this.seatObj('A3', 'A3');
        let seatObj3 = new this.seatObj('A4', 'A4');
        let seatObj4 = new this.seatObj('B1', 'B1');
        let seatObj5 = new this.seatObj('B2', 'B2');
        let seatObj6 = new this.seatObj('B3', 'B3');
        let seatObj7 = new this.seatObj('B4', 'B4');
        let seatObj8 = new this.seatObj('B5', 'B5');
        let seatObj9 = new this.seatObj('B6', 'B6');
        let seatObj10 = new this.seatObj('B7', 'B7');
        let seatObj11 = new this.seatObj('B8', 'B8');
        let seatObj12 = new this.seatObj('B9', 'B9');
        this.seats.push(seatObj0);
        this.seats.push(seatObj1);
        this.seats.push(seatObj2);
        this.seats.push(seatObj3);
        this.seats.push(seatObj4);
        this.seats.push(seatObj5);
        this.seats.push(seatObj6);
        this.seats.push(seatObj7);
        this.seats.push(seatObj8);
        this.seats.push(seatObj9);
        this.seats.push(seatObj10);
        this.seats.push(seatObj11);
        this.seats.push(seatObj12);
    }
};
</script>
